@font-face {
  font-family: 'Questrial';
  src: url('./fonts/Questrial/Questrial-Regular.ttf');
  font-weight: normal;
  font-style: normal;
}
button,
input[type="submit"],
input[type="reset"] {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}
body {
  background-color: #8dd3f3;
  font-family: 'Questrial';
  margin: 0px;
  padding: 0px;
  font-size: 18px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
button {
  background-color: #6014b8;
  padding: 10px;
  border-radius: 5px;
  color: #ffffff;
  font-weight: bold;
  box-shadow: #161616 2px 2px 2px 1px;
}
p {
  color: #474747;
  font-size: 22px;
  text-align: center;
}
p span {
  color: #ffffff;
  font-size: 26px;
}
.center-block {
  display: flex;
  align-items: center;
  justify-content: center;
}
.text-green {
  color: #3ab415;
}
.text-yellow {
  color: #ffcc5e;
}
.alta {
  overflow: hidden;
}
.alta img {
  height: 100%;
  width: auto;
}
@supports (object-fit: cover) {
  .alta img {
    height: 100%;
    object-fit: cover;
    object-position: center center;
  }
}
.ancha {
  overflow: hidden;
}
.ancha img {
  height: auto;
  width: 100%;
}
@supports (object-fit: contain) {
  .ancha img {
    height: 100%;
    object-fit: contain;
    object-position: center center;
  }
}
.btn-mlg {
  margin: 10px 5px 10px 5px;
  border-radius: 3.5px;
  padding: 8px 4px 8px 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  transition: 0.3s all ease;
}
.btn-mlg svg {
  font-size: 24px;
  margin-right: 5px;
}
.btn-mlg-yellow {
  color: #ffffff;
  text-shadow: 2px 2px 2px #343a40;
  background-color: #fab624;
  box-shadow: 1px 1px 10px rgba(50, 50, 50, 0.4);
}
.btn-mlg-yellow svg {
  font-weight: bold;
}
.btn-mlg-yellow-outline {
  color: #fab624;
  background-color: transparent;
  border: 1px solid #fab624;
  box-shadow: 1px 1px 10px rgba(50, 50, 50, 0.4);
}
.btn-mlg-blue-outline {
  color: #4889ca;
  background-color: transparent;
  border: 1px solid #4889ca;
  box-shadow: 1px 1px 10px rgba(50, 50, 50, 0.4);
}
.btn-mlg-blue {
  color: #ffffff;
  background-color: #4889ca;
  box-shadow: 1px 1px 10px rgba(50, 50, 50, 0.4);
}
.btn-mlg-transparent-white {
  color: #ffffff;
  background-color: transparent;
}
.btn-mlg-red {
  color: #ffffff;
  background-color: #f14545;
  box-shadow: 1px 1px 10px rgba(50, 50, 50, 0.4);
}
.btn-mlg-purple {
  color: #ffffff;
  background-color: #c02792;
  box-shadow: 1px 1px 10px rgba(50, 50, 50, 0.4);
}
.btn-mlg-green {
  color: #ffffff;
  background-color: #27c041;
  box-shadow: 1px 1px 10px rgba(50, 50, 50, 0.4);
}
.btn-mlg-big {
  font-size: 18px;
}
.btn-mlg-big svg {
  font-size: 30px;
  margin-right: 8px;
}
.btn-mlg-small {
  font-size: 14px;
}
.btn-mlg-small svg {
  font-size: 18px;
  margin-right: 4px;
}
.btn-mlg-minsvg {
  font-size: 10px;
}
.btn-mlg-minsvg svg {
  font-size: 13px;
  margin-left: 3px;
}
.btn-mlg:hover {
  box-shadow: 1px 1px 20px rgba(150, 150, 150, 0.2);
  margin-left: 10px;
  cursor: pointer;
}
.btn-mlg:focus {
  outline: 0;
}
.btn-mlg:active {
  transform: scale(0.9);
}
.btn-mlg-block {
  color: #ffffff;
  background-color: #687079;
}
.btn-mlg-block:hover {
  box-shadow: 0px 0px 0px rgba(150, 150, 150, 0.2);
  margin-left: 0px;
  cursor: not-allowed;
}
.btn-mlg-block:focus {
  outline: 0;
}
.btn-mlg-block:active {
  transform: scale(1);
}
.invisible-button {
  z-index: 1;
  background-color: rgba(10, 10, 10, 0.2);
  position: fixed;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}
.invisible-button:hover {
  background-color: rgba(59, 59, 59, 0.2);
  cursor: pointer;
}
